<template>
  <div id="header">
    <div class="hamburger">
      <Slide
        id="mobile_menu"
        v-bind="this.$attrs" @openMenu="openMenu" @closeMenu="closeMenu"
      >
        <a href="/" class="logo_link">
          <div class="logo_text">
            <span class="blue">ON-</span>
            <span class="black">School</span>
          </div></a
        >
        <div class="nav_links">
          <router-link to="/">Home</router-link>
          <router-link to="/About">About</router-link>
          <router-link to="/news">News</router-link>
          <router-link to="/courses">Courses</router-link>
          <router-link to="/teachers">Teachers</router-link>
          <router-link to="/Choose">Info</router-link>
        </div>
      </Slide>
    </div>
    <nav id="main_menu">
      <a href="/" class="logo_link">
        <div class="logo"></div>
        <div class="logo_text">
          <span class="blue">ON-</span>
          <span class="black">School</span>
        </div></a
      >
      <div class="nav_links">
        <router-link to="/">Home</router-link>
        <router-link to="/About">About</router-link>
        <router-link to="/news">News</router-link>
        <router-link to="/courses">Courses</router-link>
        <router-link to="/teachers">Teachers</router-link>
        <router-link to="/Choose">Info</router-link>
      </div>
    </nav>
    <div id="logo"></div>
  </div>
</template>

<script>
import { Slide } from "vue-burger-menu";
export default {
  components: {
    Slide
  },
  created() {
    let prevScrollpos = window.pageYOffset;
    window.onscroll = function () {
      let currentScrollPos = window.pageYOffset;
      if (prevScrollpos > currentScrollPos) {
        document.getElementById("main_menu").style.top = "0";
      } else {
        document.getElementById("main_menu").style.top = "-100px";
      }
      document.getElementById("main_menu").style.transition = "all .3s ease";
      prevScrollpos = currentScrollPos;
    };
  },
  methods: {
    openMenu() {
          this.$emit('openMenu');
        },
        closeMenu() {
          this.$emit('closeMenu');
        }
  }
};
</script>
